.load-more-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  color: #626262;
  border: 1px solid #626262;
  padding: 20px 25px;
  width: 165px;
  margin: 0 auto;
  margin-top: 120px;
  transition: all 300ms ease-in;
}

.load-more-btn:hover {
  background-color: #626262;
  color: #fff;
}
